import { createMutableSchema, schemaMapperFactory } from '@empathyco/x-adapter';
import { QuerySuggestionsResponse, Suggestion } from '@empathyco/x-types';
import { AlgonomyQuerySuggestionsResponse, AlgonomySuggestion } from '../responses.types';
import { algonomySuggestionSchema } from './suggestion.schema';

const mapAlgonomySuggestions = (suggestions: AlgonomySuggestion[]): Suggestion[] => {
  return suggestions.map(suggestion =>
    schemaMapperFactory(algonomySuggestionSchema)(suggestion, {})
  );
};

export const algonomyQuerySuggestionsResponseSchema = createMutableSchema<
  AlgonomyQuerySuggestionsResponse,
  QuerySuggestionsResponse
>({
  suggestions: response => {
    const algonomySuggestions = Array.isArray(response) ? response : response.suggestions;

    return mapAlgonomySuggestions(algonomySuggestions);
  }
});
