import { createMutableSchema } from '@empathyco/x-adapter';
import { Result } from '@empathyco/x-types';
import { AlgonomyResult } from '../responses.types';

export const algonomyResultSchema = createMutableSchema<AlgonomyResult, Result>({
  id: 'id',
  name: 'name',
  url: 'clickUrl',
  images: ({ imageId }) => [imageId],
  identifier: {
    value: 'id'
  },
  rating: {
    value: 'rating'
  },
  price: {
    value: ({ priceCents, salePriceCents }: AlgonomyResult) => salePriceCents ?? priceCents,
    originalValue: 'priceCents',
    futureValue: () => 0,
    hasDiscount: ({ priceCents, salePriceCents }) => salePriceCents < priceCents
  },
  type: () => 'Default',
  modelName: () => 'Result',
  description: 'description',
  brand: 'brand',
  categoryId: 'categoryId',
  categoryName: 'categoryName',
  numReviews: 'numReviews',
  releaseDate: 'releaseDate'
});
