import {
  platformAdapter,
  PlatformRecommendationsRequest,
  recommendationsRequestSchema
} from '@empathyco/x-adapter-platform';
import { RecommendationsRequest } from '@empathyco/x-types';
import { endpointAdapterFactory, schemaMapperFactory } from '@empathyco/x-adapter';
import { algonomySearchResponseSchema } from './algonomy/responses/search/searchResponse.schema';
import { algonomySearchRequestSchema } from './algonomy/requests/search.schema';
import { algonomyQuerySuggestionsRequestSchema } from './algonomy/requests/query-suggestion.schema';
// eslint-disable-next-line max-len
import { algonomyQuerySuggestionsResponseSchema } from './algonomy/responses/query-suggestion/query-suggestion.schema';

export const adapter = platformAdapter;

adapter.search = endpointAdapterFactory({
  endpoint: 'https://{extraParams.env}.richrelevance.com/rrserver/api/find/v1/{extraParams.apiKey}',
  requestMapper: schemaMapperFactory(algonomySearchRequestSchema),
  responseMapper: schemaMapperFactory(algonomySearchResponseSchema),
  defaultRequestOptions: {
    id: 'search',
    parameters: {
      internal: true
    }
  }
});

adapter.querySuggestions = endpointAdapterFactory({
  endpoint:
    // eslint-disable-next-line max-len
    'https://{extraParams.env}.richrelevance.com/rrserver/api/find/v1/autocomplete/{extraParams.apiKey}',
  requestMapper: schemaMapperFactory(algonomyQuerySuggestionsRequestSchema),
  responseMapper: schemaMapperFactory(algonomyQuerySuggestionsResponseSchema)
});

/* Code sample about how to extend the result mapper with more fields. */

declare module '@empathyco/x-types' {
  export interface Result {
    description: string;
    brand: string;
    categoryId: string[];
    categoryName: string;
    numReviews: number;
    releaseDate: Date;
  }
}

recommendationsRequestSchema.$override<
  RecommendationsRequest,
  Partial<PlatformRecommendationsRequest>
>({
  // TODO Top clicked demo endpoint breaks if it receives the scope parameter
  extraParams: ({ extraParams: { scope, ...extraParams } = {} }) => extraParams
});
