import { createMutableSchema } from '@empathyco/x-adapter';
import { Suggestion } from '@empathyco/x-types';
import { AlgonomySuggestion } from '../responses.types';

export const algonomySuggestionSchema = createMutableSchema<AlgonomySuggestion, Suggestion>({
  query: 'terms',
  key: 'id',
  modelName: (_, $context) =>
    // TODO: If there are other types of suggestions aside from query suggestions,
    //  change the right side of the ternary
    $context?.requestParameters?.query ? 'QuerySuggestion' : 'QuerySuggestion',
  facets: () => [],
  isCurated: () => false
});
