import { createMutableSchema } from '@empathyco/x-adapter';
import { SimpleFilter } from '@empathyco/x-types';
import { AlgonomyFilter } from '../../responses.types';

/**
 * Default implementation for the SimpleFilterSchema.
 *
 * @public
 */
export const algonomySimpleFilterSchema = createMutableSchema<AlgonomyFilter, SimpleFilter>({
  facetId: (_, $context) => $context?.facetId as string,
  label: 'value',
  id: 'filter',
  totalResults: 'count',
  selected: () => false,
  modelName: () => 'SimpleFilter'
});
