import { createMutableSchema } from '@empathyco/x-adapter';
import { Promoted } from '@empathyco/x-types';
import { AlgonomyLink } from '../responses.types';

/**
 * Default implementation for the PromotedSchema.
 *
 * @public
 */
export const algonomyPromotedSchema = createMutableSchema<AlgonomyLink, Promoted>({
  id: 'id',
  url: 'url',
  title: ({ title }) => title ?? '',
  image: ({ image_url }) => image_url ?? '',
  position: () => 0,
  modelName: () => 'Promoted'
});
