import { createMutableSchema, MutableSchema, schemaMapperFactory } from '@empathyco/x-adapter';
import { SearchResponse } from '@empathyco/x-types';
import { AlgonomySearchResponse } from '../responses.types';
import { algonomyResultSchema } from './result.schema';
import { algonomyBannerSchema } from './banner.schema';
import { algonomyPromotedSchema } from './promoteds.schema';
import { algonomyRedirectionSchema } from './redirection.schema';
import { algonomyFacetSchema } from './facets.schema';

const placementMapper = <T, K>(placementProperty: Array<T>, schema: MutableSchema<T, K>): K[] => {
  return placementProperty.map(element => schemaMapperFactory(schema)(element, {}));
};

/**
 * Typing for the mutable schema is set to 'any' in the algonomy side due to type recursion
 * complexity cousing problems.
 * Setting it manually in the mapping functions.
 */
// TODO: I'm assuming that there's always just 1 placement inside placements
export const algonomySearchResponseSchema = createMutableSchema<any, SearchResponse>({
  results: ({ placements }: AlgonomySearchResponse) =>
    placementMapper(placements[0]?.docs, algonomyResultSchema),
  facets: ({ placements }: AlgonomySearchResponse) =>
    placementMapper(placements[0]?.facets, algonomyFacetSchema),
  totalResults: ({ placements }) => placements[0]?.numFound,
  spellcheck: ({ placements }: AlgonomySearchResponse) => placements[0]?.spellchecked ?? '',
  banners: ({ placements }: AlgonomySearchResponse) =>
    placementMapper(placements[0]?.links.banner, algonomyBannerSchema),
  promoteds: ({ placements }: AlgonomySearchResponse) =>
    placementMapper(placements[0]?.links.sponsored, algonomyPromotedSchema),
  redirections: ({ placements }: AlgonomySearchResponse) =>
    placementMapper(placements[0]?.links.directlink, algonomyRedirectionSchema),
  // TODO: implement a way of taking your tagging url and separate the url and params
  queryTagging: ({ placements }: AlgonomySearchResponse) => ({
    url: placements[0].searchTrackingUrl,
    params: {}
  })
});
