import { createMutableSchema } from '@empathyco/x-adapter';
import { Banner } from '@empathyco/x-types';
import { AlgonomyLink } from '../responses.types';

/**
 * Default implementation for the PromotedSchema.
 *
 * @public
 */
export const algonomyBannerSchema = createMutableSchema<AlgonomyLink, Banner>({
  id: 'id',
  url: 'url',
  title: ({ title }) => title ?? '',
  image: ({ image_url }) => image_url ?? '',
  modelName: () => 'Banner'
});
