import { createMutableSchema } from '@empathyco/x-adapter';
import { QuerySuggestionsRequest } from '@empathyco/x-types';
import { AlgonomyQuerySuggestionsRequest } from './requests.types';

export const algonomyQuerySuggestionsRequestSchema = createMutableSchema<
  QuerySuggestionsRequest,
  AlgonomyQuerySuggestionsRequest
>({
  query: 'query',
  start: ({ start }) => start ?? 0,
  rows: ({ rows }) => rows ?? 0,
  lang: ({ extraParams }) => (extraParams?.lang as string) ?? 'en',
  extraParams: 'extraParams'
});
