import { createMutableSchema } from '@empathyco/x-adapter';
import { SimpleFacet } from '@empathyco/x-types';
import { AlgonomyFacet } from '../responses.types';
import { algonomySimpleFilterSchema } from './filters/simple-filter.schema';

/**
 * Default implementation for the FacetSchema.
 *
 * @public
 */
export const algonomyFacetSchema = createMutableSchema<AlgonomyFacet, SimpleFacet>({
  id: 'facet',
  label: 'facet',
  // TODO: Don't see any other models from the algonomy's facets
  modelName: () => 'SimpleFacet',
  filters: {
    $path: 'values',
    $subSchema: algonomySimpleFilterSchema,
    $context: {
      facetId: 'facet'
    }
  }
});
